.imageGallery {
    display: grid;
    max-width: calc(100% - 48px);
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    grid-gap: 16px;
    margin-top: 0;
    margin-bottom: 0;
    padding: 8px;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}

.imageGalleryItem {
    margin: 0;
    width: 150px;
    border-radius: 2px;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
        0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.colum {
    display: flex;
    flex-direction: column;
    align-items: center;
}